import './App.css';
import { Button } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import React from 'react';
import { ethers } from "ethers";
import { useEffect, useState } from 'react'
import 'sf-font';
import axios from 'axios';
import VAULTABI from './VAULTABI.json';
import NFTABI from './ABI.json';
import Web3Modal from "web3modal";
import WalletConnectProvider from "@walletconnect/web3-provider";
import WalletLink from "walletlink";
import Web3 from 'web3';
import /* eslint-disable */ DialogTitle from '@mui/material/DialogTitle';
import Dialog from '@mui/material/Dialog';


var web3 = null;
var account = null;
var vaultcontract = null;


const NFTCONTRACT = "0x899310114B573E768747ea866c0edC51F39C15Cb";
const STAKINGCONTRACT = "0xC33176cFa7095C1027CD0c1bF3322449639F59cD";
const moralisapi = "https://deep-index.moralis.io/api/v2/";
const moralisapikey = "kDivwybEmEzIpTliB1KNyc9gEuE748bYd9qZeUiXVdQeUjpiGUoMWqAeHLchRH7k";
const nftpng = "https://dweb.link/ipfs/QmaEhHLdXoTNG6vzZGuhcugpp2LCcTu2tbXaeA6fnPcWGV/";




const providerOptions = {
    binancechainwallet: {
      package: true
      },
  
      walletconnect: {
        package: WalletConnectProvider,
        options: {
          infuraId: "f5364be202cb4bf0b151d9242e079fa5"
        }
      },
  
      walletlink: {
        package: WalletLink, 
        options: {
          appName: "BULLSC-TIER1-VAULT", 
          infuraId: "f5364be202cb4bf0b151d9242e079fa5",
          rpc: "", 
          chainId: 137, 
          appLogoUrl: null, 
          darkMode: true 
        }
        },
  
    };
  
    const web3Modal = new Web3Modal({
      network: "polygon",
      theme: "dark",
      cacheProvider: true,
      providerOptions 
      });
  

      










export default function NFT2() {
  const [apicall, getNfts] = useState([])
  const [nftstk, getStk] = useState([])
  const [loadingState, setLoadingState] = useState('not-loaded');
  const [open,setOpen] = useState(false);
  const [tokenID,setTokenID] = useState(0);
  const [address,setAddress] = useState("");
const handleClose = (value) => {
  setOpen(false);

};
  useEffect(() => {
    callApi()
  }, [])
  const transfer = (tokenID) => {

    setTokenID(tokenID);
    setOpen(true);
  }
  const sendNFT = async() => {

    console.log(tokenID);
    console.log(address);
    var provider = await web3Modal.connect(window.ethereum);
    web3 = new Web3(provider);
    var accounts = await web3.eth.getAccounts();
    account = accounts[0];
    console.log(accounts);
    const NFTcont= new web3.eth.Contract(NFTABI, NFTCONTRACT);
    let x =await NFTcont.methods.transferFrom(account,address,tokenID);
    const transactionParameters = {
      to: NFTCONTRACT, // Required except during contract publications.
      from: window.ethereum.selectedAddress, // must match user's active address.
  //gasLimit: web3.utils.toHex(web3.utils.toWei('50','gwei')),  
  //gasPrice: web3.utils.toHex(web3.utils.toWei('60','gwei')), 
      maxPriorityFeePerGas: web3.utils.toHex(web3.utils.toWei('31','gwei')),
      maxFeePerGas: web3.utils.toHex(web3.utils.toWei('35','gwei')),
      gas: ethers.BigNumber.from(300000).toHexString(),
      'data': NFTcont.methods.transferFrom(account,address,tokenID).encodeABI() //make call to NFT smart contract 
  //Web3.utils.toBN(Web3.utils.toWei(val, "ether")).toString(16)
  };
  //sign transaction via Metamask
  try {
      const txHash = await window.ethereum.request({
              method: 'eth_sendTransaction',
              params: [transactionParameters],
          });
      // console.log(txHash);
      return {
          success: true,
          status: "âœ… Check out your transaction on Etherscan: https://ropsten.etherscan.io/tx/" + txHash
      }
  } catch (error) {
      return {
          success: false,
          status: "ðŸ˜¥ Something went wrong: " + error.message
      }
  }
  }

  async function callApi() {
    var provider = await web3Modal.connect();
    web3 = new Web3(provider);
    await provider.send('eth_requestAccounts');
    var accounts = await web3.eth.getAccounts();
    account = accounts[0];
    vaultcontract = new web3.eth.Contract(VAULTABI, STAKINGCONTRACT);
		let config = {'X-API-Key': moralisapikey, 'accept': 'application/json'};
		const nfts = await axios.get((moralisapi + `/nft/${NFTCONTRACT}/owners?chain=polygon&format=decimal`), {headers: config})
    .then(output => {
        const { result } = output.data
        return result;
      })
    const apicall = await Promise.all(nfts.map(async i => {
      let item = {
        tokenId: i.token_id,
        holder: i.owner_of,
        wallet: account,
      }
      return item
    }))
    const stakednfts = await vaultcontract.methods.tokensOfOwner(account).call()
    .then(id => {
      return id;
    })
    const nftstk = await Promise.all(stakednfts.map(async i => {
      let stkid = {
        tokenId: i,
      }
      return stkid
    }))
      getNfts(apicall)
      getStk(nftstk)
      console.log(apicall);
      setLoadingState('loaded')
    } 
    if (loadingState === 'loaded' && !apicall.length) 
    return (
          <h1 className="text-3xl">Wallet Not Connected</h1>)
   
   
   
   
   
   
   
   
          return (
            <div className='nftportal mb-4'>
                <div className="container col-lg-11">
                  <div className="row items px-3 pt-3">
                    <div className="ml-3 mr-3" style={{ display: "inline-grid", gridTemplateColumns: "repeat(4, 5fr)", columnGap: "20px" }}>
                      {apicall.map((nft, i) => {
                        var owner = nft.wallet.toLowerCase();
                          if (owner.indexOf(nft.holder) !== -1) {
                        async function stakeit() {
                          vaultcontract.methods.stake([nft.tokenId]).send({ from: account });
                        }
                        return (
                          <div className="card nft-card mt-3 mb-3" key={i} >
                            <div className="image-over">
                              <img className="card-img-top" src={nftpng + nft.tokenId + '.png'} alt="" />
                            </div>
                            <div className="card-caption col-12 p-0">
                              <div className="card-body">
                                <h5 className="mb-0">YELLOW-COW NFT #{nft.tokenId}</h5>
                                <h5 className="mb-0 mt-2"><p style={{ color: "#39FF14", fontWeight: "bold", textShadow: "1px 1px 2px #000000" }}>Ready to Stake</p></h5>
                                <div className="card-bottom d-flex justify-content-between">
                                  <input key={i} type="hidden" id='stakeid' value={nft.tokenId} />
                                  <Button style={{ marginLeft: '2px', backgroundColor: "#ffffff10" }} onClick= {() => transfer(nft.tokenId)}>Send</Button>
                                  <Button style={{ marginLeft: '2px', backgroundColor: "#ffffff10" }} onClick={stakeit}>Stake</Button>
                                </div>
                              </div>
                            </div>
                          </div>
                        )}})}
                        {nftstk.map((nft, i) => {
                          async function unstakeit() {
                            vaultcontract.methods.unstake([nft.tokenId]).send({ from: account });
                          }
                          return (
                            <div>
                            
                            <div className="card stakedcard mt-3 mb-3" key={i} >
                              <div className="image-over">
                              <img style={{position:'absolute',top:'0.02rem',width:'100px'}} src='stakeicon.png' alt=""></img>
                                <img className="card-img-top" src={nftpng + nft.tokenId + '.png'} alt="" />
                              </div>
                              <div className="card-caption col-12 p-0">
                                <div className="card-body">
                                  <h5 className="mb-0">YELLOW-COW NFT #{nft.tokenId}</h5>
                                  <h5 className="mb-0 mt-2"><p style={{ color: "#15F4EE", fontWeight: "bold", textShadow: "1px 1px 2px #000000" }}>Currently Staked</p></h5>
                                  <div className="card-bottom d-flex justify-content-between">
                                    <input key={i} type="hidden" id='stakeid' value={nft.tokenId} />
                                    <Button style={{ marginLeft: '5px', backgroundColor: "#15F4EE" }} onClick={unstakeit}>Unstake</Button>
                                  </div>
                                </div>
                              </div>
                            </div>
                            </div>
                          )})}
                    </div>
                  </div>
                </div>
                <Dialog onClose={handleClose} open={open}>
                <input  type="text" value={address} onChange={(ev) => setAddress(ev.target.value)}/>
                <Button style={{ marginLeft: '5px', backgroundColor: "#15F4EE" }} onClick={sendNFT}>Transfer</Button>
          
        </Dialog>
                </div>
            )
        }
        